@import "globals";
@import "color-light";
//@import "color-dark";

.middle {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
a.btn:hover{ color: white !important; text-decoration: underline !important; }
.h{
  font-family: 'Raleway';
  color: $contrast;
  text-transform: uppercase;
}

body{
  background-color: $baselight;
  font-family: 'Raleway', sans-serif;
  color: $color;
}

#sidebar {
  position: fixed;
  height: 100%;

  #menu {
    background-color: $basedark;
    height: 100%;
    overflow: hidden;
    width: 240px;
    float: left;
    .menu-retract {
      i {
        color: $color;
        font-size: 17px;
        float: right;
        margin: 15px 20px;
        cursor: pointer;
      }
    }
    ul li ul > li > a { text-transform: lowercase;  }
    ul {
      list-style-type: none;
      margin-top: 250px;
      padding-bottom: 8px;
      li {
        a {
          color: $color;
          font-size: 16px;
          text-transform: uppercase;
        }
        &.active {
          ul { max-height: 500px !important; }
        }
        ul {
          margin: 0px 15px;
          max-height: 0px;
          overflow: hidden;
          @include transition(all, .5s);
          li {
            @include transition(all, .3s);
            padding: 2px 0px;

            &:hover {
              padding: 2px 7px;
              a {
                color: $colorlight;
              }
            }
            a {
              font-size: 16px;
            }
          }
        }
      }
    }
  }

  #nav-tabs {
    width: 155px;
    height: 100%;
    background-color: $basemedium;
    float: right;
    ul {
      list-style-type: none;
      margin: 0px;
      li {
        padding: 10px 12px;
        position: relative;
        i {
          position: absolute;
          font-size: 10px;
          top:14px;
          color:transparent;
        }
        a {
          text-align: right;
          font-size: 13px;
          display: block;
          text-transform: lowercase;
        }

        &:hover {
          i { color:$colorlight; }
          a {
            color: $colorlight;
          }
        }

        &.active {
          background-color: $baselight;
          i { color:$contrast ; }
          a{
            color:$contrast ;
            font-weight: 600;
            text-transform: lowercase;
          }
        }
      }
    }
  }
}
.content { 
  display: none; &.active{ display: block; } 
}
#content,#modal {
  width: calc(100% - 417px);
}
#content,#modal,#login{
  margin-left: 417px;
  padding: 25px 0px;
  overflow: auto;
  h3{
    font-size:22px;
    margin-bottom:25px;
    @extend .h;
    small { color: $contrast; }
  }

  h4{
    font-size: 18px;
    @extend .h;
    text-transform: none;
  }

  form{
    margin-top: 20px;
    color: $contrast;
    h4{ padding: 12px 0px; }
    
    .column {
      min-height: 55px;
    }

    select{
     background-color:$baselight;
     border: 1px solid $contrast;
     color: $contrast;
     font-size: 11px;
     height: 37px;
     padding: 5px;
     text-transform: uppercase;

     option { background-color: transparent; color: $contrast; }
   }

   .btn{
    color: $contrast;
    font-size: 12px;
    text-transform: uppercase;
    border: 1px solid $contrast;
    padding: 7px 10px;
    display: block;
    width: 128px;
  }

  input[type=text],input[type=password],textarea{
    box-shadow:none;
    background-color:$baselight;
    border: 1px solid $contrast;
    color: $contrast;
  }
  label{
    color:$contrast;
    font-family: 'Raleway';
  }

  span{
    display: block;
    width: 100%;
    color:$contrast;
    border-bottom: 1px solid $contrast;
    padding: 7px;
    margin-bottom: 20px;
    font-size: 14px;
  }

  p{
    font-size: 14px;
  }
}
}
.declaracao{
  font-size: 14px;
  clear: both;
  .column{
    padding: 0px 15px;
    min-height: 31px !important;
    &.large-2{
      width: 140px;
    }
    &.large-10 {
      width: calc(100% - 140px);
    }
    p {
      margin-bottom: 0px;
    }
  }
}

.tabs{
  border-bottom: 1px solid $contrast;
  width: calc(100% - 20px);

  .tab-title{
    &.active{ border: 1px solid $contrast; border-bottom-color: transparent; }

    a,&.active a{
      color: $contrast;
      background-color: transparent;
      outline: none;

      &:hover{
        background-color: #363636;
      }
    }
  }
}

#loading-bar{
  position: absolute;
  width: 100%;
}

#modal-bg{
  background-color:  rgba(0, 0, 0, 0.50);
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  z-index: 1000;
  display: none;
}

#modal{
  background-color: $basemedium;
  position: fixed;
  padding: 15px 25px;
  height: 100%;
  width: calc(100% - 517px);
  left: calc(100% + 517px);
  top:0px;
  @include transition(all, .4s);
  -webkit-box-shadow: 0px 0px 5px 5px black;
  -moz-box-shadow: 0px 0px 5px 5px black;
  box-shadow: 0px 0px 5px 5px black;
  z-index: 1001;

  &.active{ left: 100px; }

  .modal-top {
    position: relative;
    z-index: 1002;
    display: none !important;
    .close{
      position: absolute;
      right: 25px;
      top: 15px;
      font-size: 23px;
      color: $contrast;
      cursor: pointer;
      display: none;
    }
  }
}

.btn{
  color: $contrast;
  font-size: 12px;
  text-transform: uppercase;
  border: 1px solid $contrast;
  padding: 7px 10px;
  font-family: 'Raleway', sans-serif;
  background-color: #008CBA;
  float:left;
  margin: 5px 0px 5px 5px;
  @include transition(all, .3s);

  &.btn-icon{
    padding: 10px 10px;
  }
}

.list-nav{
  .column{ padding:0px; margin-bottom: 30px; }
  i.fa {
    cursor: pointer;
    color: $contrast;
    margin-top: 10px;
  }
  .search{
    input{ display: none; }
  }
}

table{
  width: 100%;
  background-color: transparent;
  border: 0px;
  border-spacing: 0px;

  thead{
    background: #5A6269;
    tr th {
      color: #FFF;
    }
  }
  tr {
    color: $contrast;
    &:nth-of-type(even) {
      background-color: $baselighter;
    }
    td {
      color: $contrast;
    }
  }
}

.btn:active,.btn:focus{ color: $contrast; }


.btn.btn-green {
  background-color: rgb(77, 181, 77);
}

.btn.btn-red {
  background-color: rgb(216, 60, 60);
}

.btn.btn-large{
  padding: 17px 30px;
  margin-right: 10px;
}

.pull-left{
  float: left;
}

.pull-right{
  float: right;
}

#actions{
  background-color: $baselighter;
  width: calc(100% - 395px);
  height: 100px;
  position: fixed;
  bottom: 0px;
  margin-left: -22px;
  padding-top: 22px;

  .status{
    color: $contrast;
    padding:16px 3px;
    margin-left: 150px;
    width: 160px;
    font-size: 13px;
    text-transform: uppercase;
    font-weight: 800;
    &.pendente{ color: #F2B56A; border-bottom: 1px solid #F2B56A; }

  }
}

small { color: $contrast !important;}

#preview {
  top: 30px;
  img {
    width: 80%;
    display: block;
    margin: 0 auto;
    top: 10px;
    position: relative;
  }
}

#modal{
  #actions { width: calc(100% - 537px) !important; }
}

.dashboard-panel{
  background-color: white;
  padding: 35px;
  overflow: hidden;
  margin-right: 15px;
  margin-left: 3px;
  margin-bottom: 50px;
  box-shadow: $shadow;
  width: 48%;
  float: left;
}

.color a {
  height: 20px;
  width: 20px;
  display: block;
  position: fixed;
  bottom: 5px;
  left: 5px;
  border: 1px solid gray;
}